}
body.is-modal-active .main:before {
  content: '';
  display: block;
  position: absolute;
  background-color: rgba(0,0,0,0.42);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 5;
