}
.wrapper {
  flex: 1 1 auto;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
}
.main {
  flex: 1 1 auto;
  position: relative;
  background-color: #f7f8fc;
}
.main:before {
  content: '';
  display: block;
  position: absolute;
  background-color: rgba(0,0,0,0.42);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s linear;
}
.header,
.footer {
  flex-shrink: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
a,
button,
input,
textarea,
select {
  outline: none;
  border: 0;
  font-family: inherit;
  transition: all 0.25s ease-out;
}
a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
button {
  display: inline-block;
  background: none;
}
button,
button[type="submit"],
input[type="submit"] {
  cursor: pointer;
}
input,
textarea {
  display: block;
  width: 100%;
  line-height: 1;
  background: none;
  padding: 0;
}
input[type="search"] {
  border-radius: 0;
  -webkit-appearance: none;
}
input[type="search"]::webkit-search-decoration {
  -webkit-appearance: none;
}
textarea {
  resize: none;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
  font-weight: inherit;
}
.iframe {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 35px;
  height: 0;
  overflow: hidden;
}
.iframe iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.tab-container {
  display: none;
  opacity: 0;
  animation: fadein 0.25s linear 1 forwards;
}
@include transit();
.tab-container.active {
  display: block;
  opacity: 1;
}
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
.table {
  overflow-x: auto;
}
.hidden {
  visibility: hidden;
  opacity: 0;
  z-index: -1;
}
@-moz-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
