}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 215px;
  height: 72px;
  text-align: center;
  position: relative;
  z-index: 1;
  text-decoration: none;
  margin: 0 auto;
  transition: all 0.3s linear;
}
@media (max-width: 767px) {
  .logo {
    width: 150px;
  }
}
.logo:before {
  content: '';
  display: block;
  position: absolute;
  width: 138px;
  height: 138px;
  border-radius: 50%;
  top: -48px;
  left: 50%;
  margin-left: -69px;
  background-color: #5291ff;
  z-index: 1;
  transition: all 0.5s cubic-bezier(0.52, 1.64, 0.37, 0.46);
}
.logo.is-active {
  position: relative;
  border-radius: 0;
  height: 72px;
  top: 0;
}
.logo.is-active:before {
  width: 215px;
  border-radius: 0;
  top: 0;
  height: 100%;
  left: 0;
  margin-left: 0;
  transition: all 0.5s ease;
}
@media (max-width: 767px) {
  .logo.is-active:before {
    width: 150px;
  }
}
.logo.is-active:hover:before {
  transform: none;
}
.logo.is-moveup {
  transform: translateY(-130%);
}
.logo-name {
  display: block;
  position: relative;
  z-index: 5;
  width: 100%;
  font-family: 'Gilroy', 'Arial', 'Helvetica', sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  color: #fff;
}
.logo:hover:before {
  transform: scale(1.2);
