.modal {
  background: none;
  position: fixed;
  bottom: 0;
  right: 0;
  top: 100px;
  width: 100%;
  max-width: 1026px;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  z-index: 100;
  transition: all 0.6s linear;
}
@media (max-width: 1199px) {
  .modal {
    max-width: 100%;
  }
}
.modal--thin {
  max-width: 565px;
}
@media (max-width: 767px) {
  .modal--thin {
    max-width: 100%;
  }
}
.modal.is-active {
  transform: translateX(0);
}
.modal.is-active .modal-close {
  transform: translateY(0);
}
.modal-close {
  display: block;
  width: 32px;
  height: 32px;
  background-color: #fff;
  box-shadow: 0 2px 20px 0 rgba(0,0,0,0.2);
  border-radius: 50%;
  position: absolute;
  right: 30px;
  top: -16px;
  transform: translateY(-16px);
  z-index: 5;
  transition: all 0.3s ease-in-out 0.5s;
}
@media (max-width: 767px) {
  .modal-close {
    right: 20px;
  }
}
.modal-close .icon {
  display: block;
  fill: #d0021b;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.modal-body {
  background-color: #fff;
  overflow: hidden;
  border-radius: 5px 0 0 0;
  height: 100%;
  padding: 0 0 0 30px;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.25s ease-out;
}
@media (max-width: 991px) {
  .modal-body {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .modal-body {
    padding: 0 0 0 18px;
  }
}
.modal-body--thin {
  padding: 0;
}
